/**
 * System imports
 ============================================================================ */
import React from "react";
import { Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import SubmitButton from "../../../submitButton";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "LeadsLogIn" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  const { setRedirectTo } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Return
    ============================================================================ */
  return (
    <div className="bd-masthead">
      <h1>Transfer</h1>

      <p className="lead">
        Quickly access local and regional market environments with
        Local-Currency.com, the worldâ€™s first local trading management platform,
        featuring one-click-allowance, instant transfers, business toolbox, and
        powerful currency management.
      </p>

      <br />

      <Container fluid>
        <Row>
          <Col style={{ paddingLeft: 0 }}>
            <SubmitButton
              {...args}
              caption="Get started"
              request="get started"
              namespace="LogIn"
              onSubmit={() => setRedirectTo("/sign-up")}
            />
          </Col>
          <Col style={{ paddingRight: 0 }}>
            <SubmitButton
              {...args}
              caption="Documentation"
              request="get started"
              namespace="LogIn"
              variant="secondary"
            />
          </Col>
        </Row>
      </Container>

      <br />
    </div>
  );
};
